* {
  box-sizing: border-box;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.btn-home {
  z-index: 3;
  padding: 10px 16px;
  position: fixed;
  top: 10px;
  left: 30px;
}

.search-form {
  z-index: 2;
  width: 100vw;
  background-color: #3d4e83;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 12px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, .6);
}

.search-form input[name="searchQuery"] {
  width: 400px;
  height: 40px;
  border-style: none;
  border-radius: 4px;
  padding-left: 12px;
  padding-right: 12px;
  font-size: 20px;
}

.search-form input[name="searchQuery"]:focus {
  outline: 2px solid #5392f2;
}

.search-form button[type="submit"] {
  width: 100px;
  height: 40px;
  letter-spacing: .04em;
  background-color: #334764;
  border-style: none;
  border-radius: 4px;
  margin-left: 10px;
  font-weight: 600;
}

.search-form button[type="submit"]:hover, .search-form button[type="submit"]:focus {
  color: #1e1e1e;
  background-color: #eaeaea;
}

.gallery {
  width: calc(100vw + 24px);
  flex-wrap: wrap;
  justify-content: center;
  margin-top: -24px;
  margin-bottom: 0;
  margin-left: -24px;
  padding-top: 85px;
  padding-left: 0;
  padding-right: 24px;
  display: flex;
}

.photo-card-wrapper {
  width: 390px;
  margin-top: 24px;
  margin-left: 24px;
  list-style: none;
}

.photo-card {
  transition: transform var(--animation-duration) var(--timing-function);
  border-radius: 0 0 4px 4px;
  flex-grow: 1;
  display: block;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .3);
}

.photo-card:hover {
  transform: scale(1.03);
}

.photo-card img {
  width: 100%;
  aspect-ratio: 390 / 240;
  object-fit: cover;
}

.photo-card .info {
  text-align: center;
  color: #1e1e1e;
  justify-content: space-between;
  padding: 12px 15px;
  display: flex;
}

.photo-card .info:hover, .photo-card .info:focus {
  cursor: pointer;
}

.photo-card .info .info-item {
  flex-direction: column;
  margin-bottom: 0;
  display: flex;
}

.load-more-container {
  text-align: center;
}

.load-more {
  color: #1e1e1e;
  text-transform: uppercase;
  letter-spacing: .08em;
  background-color: #eaeaea;
  border-style: none;
  border-radius: 4px;
  margin: 32px auto 24px;
  padding: 12px 24px;
  font-weight: 600;
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #3d4e83;
}

.load-more.hidden {
  display: none;
}

body.hidden-scroll {
  overflow: hidden;
}

